@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
:root {
  --font-body: "Poppins";
  --header-height: 3.25rem;

  --color-hue: 160;
  --color-primary: hsl(var(--color-hue), 70%, 60%);
  --color-primary-two: hsl(var(--color-hue), 55%, 55%);
  --color-primary-light: hsl(var(--color-hue), 90%, 85%);
  --color-gradient: linear-gradient(
    60deg,
    var(--color-primary-light),
    var(--color-primary)
  );

  --color-title: hsl(var(--color-hue), 8%, 15%);
  --color-text: hsl(var(--color-hue), 8%, 45%);
  --color-text-light: hsl(var(--color-hue), 8%, 65%);

  --color-input: hsl(var(--color-hue), 70%, 95%);
  --color-body: hsl(var(--color-hue), 60%, 99%);
  --color-container: #fff;

  --font-size-0-75: 0.75rem;
  --font-size-1: 1rem;
  --font-size-1-25: 1.25rem;
  --font-size-1-5: 1.5rem;
  --font-size-2: 2rem;
  --font-size-2-5: 2.5rem;
  --font-size-3: 3rem;

  --margin-0-5: 0.5rem;
  --margin-0-75: 0.75rem;
  --margin-1: 1rem;
  --margin-1-25: 1.25rem;
  --margin-1-5: 1.5rem;
  --margin-2: 2rem;
  --margin-3: 3rem;

  --width-desktop: 1280px;
  --width-tablet: 768px;

  --z-index-1: 1;
  --z-index-10: 10;
  --z-index-100: 100;
}

* {
  box-sizing: border-box;
}

body {
  color: hsl(160, 8%, 45%);
  color: var(--color-text);
  background-color: hsl(160, 60%, 99%);
  background-color: var(--color-body);
  font-size: 1rem;
  font-size: var(--font-size-1);
  font-family: "Poppins";
  font-family: var(--font-body);
  margin: 0;
}

body.dark-mode {
  --color-primary-two: hsl(var(--color-hue), 30%, 5%);
  --color-title: hsl(var(--color-hue), 8%, 95%);
  --color-text: hsl(var(--color-hue), 8%, 75%);
  --color-input: hsl(var(--color-hue), 10%, 15%);
  --color-body: hsl(var(--color-hue), 8%, 10%);
  --color-container: hsl(var(--color-hue), 30%, 15%);
}

h1,
h2,
h3 {
  color: hsl(160, 8%, 15%);
  color: var(--color-title);
  align-items: center;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

html {
  scroll-behavior: smooth !important;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 0;
  min-height: 100vh;
  max-width: var(--width-desktop);
}

.section-title {
  font-size: var(--font-size-3);
}

.section-subtitle {
  font-size: var(--font-size-0-75);
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 2rem;
  border: none;
  font-family: var(--body-font);
  font-weight: 600;
  cursor: pointer;
  transition: 0.4s;
  width: 100%;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.btn:hover {
  transform: translateY(-2px);
  opacity: 0.9;
}

.btn:disabled {
  cursor: not-allowed;
  background-color: var(--color-text-light);
}

.btn-primary {
  color: black;
  background-color: var(--color-primary);
}

.btn-primary .icon {
  fill: #fff;
  height: 1.5rem;
  margin-left: 0.5rem;
}

.btn-outline {
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
  background-color: var(--color-body);
}

.btn-outline .icon {
  fill: var(--color-title);
  margin-left: 0.5rem;
}

.about-item {
  background: var(--color-input);
  flex: 1 1;
  height: 100%;
  margin: 1rem 0;
  padding: 2rem;
  transition: 0.5s ease;
  border-right: 4px solid hsl(var(--color-hue), 70%, 95%, 0.1);
}

.about:hover .about-item {
  opacity: 0.3;
}

.about .about-item:hover {
  opacity: 1;
  transform: scale(1.1);
  cursor: pointer;
  border-right: 4px solid var(--color-primary);
}

.about .about-item:hover .about-item-title::before {
  width: 3.5rem;
}

.about-item-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
}

.about-item-title::before {
  position: absolute;
  content: "";
  bottom: 0;
  width: 2rem;
  height: 2px;
  background-color: var(--color-primary);
  transition: 0.5s ease;
}

.about-item-content {
  display: flex;
  align-items: baseline;
  margin: 1rem 0;
  font-weight: 600;
}

.about-item-content-wrapper {
  flex: 1 1;
}

.about-item-content-date {
  margin-right: 2rem;
  text-transform: uppercase;
  font-size: 0.75rem;
  color: var(--color-primary);
  letter-spacing: 2px;
  width: 6rem;
}

.about-item-content-headline {
  margin: 0;
  color: var(--color-title);
  font-size: 1.25rem;
  letter-spacing: 1px;
}

.about-item-content-description {
  margin: 0;
  text-transform: uppercase;
  font-size: 0.825rem;
}

#about .btn-outline {
  margin-top: 2rem;
  width: 100%;
  text-align: center;
}

.skills-container {
  position: relative;
  padding: 2rem;
  margin: 2rem 0;
  border: 1px solid var(--color-primary);
  background-color: var(--color-body);
}

.skills-container:hover .item-title::before {
  width: 3.5rem;
}

.skill-badge {
  color: var(--color-primary);
}

.skill-icon {
  top: -0.75rem;
  padding: 0 1rem;
  background-color: var(--color-body);
  position: absolute;
}

.skill-icon .icon {
  fill: var(--color-primary);
}

.skill-bars {
  margin: 0 0 4rem;
}

.skill-bar {
  position: relative;
  width: 100%;
  height: 4px;
  background-color: var(--color-input);
  z-index: 1;
}

.skill-bar-label {
  margin: 0;
  font-size: 0.825rem;
  margin: 1.5rem 0 0.125rem;
  color: var(--color-title);
}

.skill-bar-percent {
  position: absolute;
  top: 0;
  width: 50%;
  height: 4px;
  background-color: var(--color-primary);
}

.item-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
}

.item-title::before {
  position: absolute;
  content: "";
  bottom: 0;
  width: 2rem;
  height: 2px;
  background-color: var(--color-primary);
  transition: 0.5s ease;
}

.item-desc {
  text-align: justify;
}

.tool {
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex: 1 1;
  min-width: 6rem;
  min-height: 7rem;
  padding: 0.5rem;
  background-color: var(--color-input);
  transition: 0.25s ease;
  cursor: pointer;
}

.tool-label {
  font-weight: 600;
  letter-spacing: 2px;
  position: relative;
}

.tool-label::before {
  position: absolute;
  content: "";
  bottom: 0;
  width: 1rem;
  height: 2px;
  background-color: var(--color-primary);
  transition: 0.5s ease;
}

.tool .icon {
  fill: var(--color-primary);
}

.tool:hover {
  transform: scale(1.1);
  background-color: var(--color-container);
}

.tool:hover .tool-label::before {
  width: 2rem;
}

.tools {
  display: grid;
  grid-gap: 0.25rem;
  grid-auto-columns: minmax(0, 1fr);
  grid-auto-flow: column;
  grid-template-areas:
    "a . b"
    ". c d"
    "e f g"
    ". h .";
}

#a {
  grid-area: a;
}

#b {
  grid-area: b;
}

#c {
  grid-area: c;
}

#d {
  grid-area: d;
}

#e {
  grid-area: e;
}

#f {
  grid-area: f;
}

#g {
  grid-area: g;
}

#h {
  grid-area: h;
}

.form-field {
  margin-bottom: 1.5rem;
}

input,
textarea {
  resize: none;
  border: 1px solid transparent;
  box-sizing: border-box;
  outline: none;
  width: 100%;
  padding: 1rem;
  margin: 0.25rem 0;
  font-family: var(--body-font);
  color: var(--color-title);
  background-color: var(--color-input);
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: var(--color-text-light);
}

input::placeholder,
textarea::placeholder {
  color: var(--color-text-light);
}

.form-field-label {
  align-items: baseline;
  display: inline-flex;
}

label {
  text-transform: uppercase;
  font-size: 0.825rem;
  font-weight: 600;
  margin-right: 0.5rem;
  color: var(--color-title);
}

.input-error-field {
  border: 1px solid var(--color-primary);
}

.input-error-message {
  font-size: 0.825rem;
  color: var(--color-primary);
}

.contact-form-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 8px;
  padding: 0.5rem;
  transform: translate(-50%, -50%) !important;
  margin: auto auto;
  background-color: var(--color-container);
}

.contact-form-modal-content {
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.contact-form-modal .icon-close {
  height: 1.75rem;
  float: right;
}

.icon-success {
  cursor: auto;
  cursor: initial;
  height: 5rem;
  fill: var(--color-primary);
}

@media screen and (max-width: 767px) {
  .contact-form-modal {
    width: 100%;
    height: 100%;
  }

  .icon-close {
    margin: 1rem 1rem 0 0;
  }
}

.contact-form {
  margin: 1rem 0;
}

.contact-form button > svg {
  fill: #fff !important;
  height: 1.25rem !important;
  animation: rotating 1s linear infinite;
}

.contact-form button > svg:hover {
  fill: #fff;
  cursor: none;
}

@media screen and (max-width: 768px) {
  .contact-form button {
    width: 100%;
    justify-content: center;
  }
}

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}

.sending-mails {
  z-index: 1;
  position: absolute;
}

@media screen and (max-width: 768px) {
  .contact-container {
    flex-direction: column;
  }

  .contact-form button {
    width: 100%;
    justify-content: center;
  }
}

.icon {
  height: 1.5rem;
  fill: var(--color-text);
  cursor: pointer;
}

.icon:hover {
  fill: var(--color-primary);
}

.header {
  margin: 2.5rem 0;
  z-index: var(--z-index-100);
}

.nav {
  font-size: 0.825rem;
  font-weight: 600;
  color: var(--color-title);
  max-width: var(--width-desktop);
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 100%;
}

.nav-left {
  display: flex;
  align-items: center;
}

.night-mode-switch {
  display: flex;
}

.nav-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.nav-item {
  padding: 1rem 0;
  transition: 0.5s;
  display: flex;
}

.dash {
  width: 4.5rem;
  height: 2px;
  margin: 1rem;
  background-color: var(--color-title);
  transition: 0.5s ease;
}

.number {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-input);
  width: 2.25rem;
  height: 2.25rem;
}

.nav-link {
  color: var(--color-title);
  display: flex;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.nav-link:hover {
  color: var(--color-primary);
}
.nav-link:hover .dash {
  background-color: var(--color-primary);
}

.active .dash {
  width: 7rem;
  background-color: var(--color-primary);
}

.active a {
  color: var(--color-primary);
}

.nav-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3rem;
  height: 3rem;
  background-color: var(--color-primary);
  z-index: 2;
}

.nav-toggle .icon {
  fill: black;
}

.nav-close {
  position: absolute;
  right: 1.5rem;
  top: 1.5rem;
  height: 1.5rem;
  fill: var(--color-text);
}

.nav-menu {
  height: 100%;
}

@media screen and (max-width: 767px) {
  .nav-menu {
    width: 100%;
    height: auto;
    height: initial;
    position: fixed;
    bottom: -100%;
    left: 0;
    padding: 3rem;
    transition: 0.5s;
    background-color: var(--color-body);
    box-sizing: border-box;
  }

  .nav-list {
    align-items: center;
  }

  .nav-item {
    margin: 1rem;
  }

  .dash,
  .number {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .nav-toggle,
  .nav-close {
    display: none;
  }
}

.show-menu {
  bottom: 0;
  z-index: 3;
}

.color-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 1rem;
  transform: translate(-50%, -50%) !important;
  margin: auto auto;
  background-color: var(--color-body);
  outline: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.color-modal-title {
  margin: 0 0 1rem;
}

.color-dropdown-list {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  flex-wrap: wrap;
  max-width: 30rem;
  padding: 1rem 0;
}

.color-dropdown-item {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 1.5rem;
  height: 1.5rem;
  transition: 0.5s ease;
  overflow: hidden;
  position: relative;
}

.color-dropdown-item .icon {
  fill: black;
  width: 1rem;
  height: 1rem;
}

.color-dropdown-item:hover {
  transform: scale(1.5);
}

.color-modal .icon-close {
  height: 1.75rem;
  position: absolute;
  top: 1rem;
  right: 1.5rem;
}

@media screen and (max-width: 767px) {
  .color-modal {
    width: 100%;
    height: 100%;
  }
}

.color-dropdown-header {
  position: relative;
  width: 2.25rem;
  height: 2.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-input);
  cursor: pointer;
}

.progress-bar {
  position: absolute;
  content: "";
  top: 50%;
  margin-top: 5vh;
  right: 0;
  width: 2px;
  height: 50%;
  background: linear-gradient(60deg, var(--color-body), var(--color-primary));
  border-radius: 8px;
}

.progress-bar::before {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  left: -3px;
  top: 0;
  z-index: 1;
  transform: rotate(45deg);
  background-color: var(--color-primary);
}

.progress-bar-pulse {
  position: absolute;
  content: "";
  width: 8px;
  height: 8px;
  left: -3px;
  top: 0;
  opacity: 0;
  border: 1px solid var(--color-primary-light);
  animation: pulsate 1s 0.35s ease-out;
  animation-iteration-count: 3;
}

.progress-bar-percent {
  transition: 0.3s;
  left: 0;
  width: 2px;
  border-radius: 8px;
  background: linear-gradient(
    60deg,
    var(--color-primary-light),
    var(--color-body)
  );
  position: absolute;
  bottom: 100%;
}

@media screen and (max-width: 767px) {
  .progress-bar {
    display: none;
  }
}

.night-mode-switch {
  width: 2.25rem;
  height: 2.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: var(--color-input);
}

.container {
  display: flex;
  flex-wrap: wrap;
  max-width: var(--width-desktop);
}

.column-right {
  flex: 1 1;
  padding: 5vh 2rem;
  position: relative;
}

.column-left {
  top: 0;
  padding: 5vh 2rem;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  justify-content: space-between;
  flex: 1 1;
  min-width: 300px;
  max-height: 90vh;
  flex-direction: column;
  align-items: flex-start;
}

.actions {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}

.typewriter-headline {
  font-size: 2.75rem;
  margin: 0;
  color: var(--color-primary);
}

.title {
  margin: 0;
  font-size: 2.5rem;
}

.desc {
  text-align: justify;
  line-height: 2;
}

.info {
  justify-content: space-between;
  display: flex;
  width: 100%;
}

.contact-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contact-link {
  font-size: 0.825rem;
  display: flex;
  align-items: center;
  text-decoration: underline;
  color: var(--color-text);
}

.contact-link-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.25rem;
  height: 2.25rem;
  margin: 0 0.5rem;
  background: var(--color-input);
}

.contact-link:hover,
.contact-link:hover svg {
  fill: var(--color-primary);
  color: var(--color-primary);
  cursor: pointer;
}

.my-photo {
  width: 5rem;
  height: 5rem;
  background: var(--color-input);
}

@keyframes pulsate {
  0% {
    transform: rotate(45deg) scale(0.1, 0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(45deg) scale(3, 3);
    opacity: 0;
  }
}

.row {
  display: flex;
}

@media screen and (max-width: 767px) {
  .container {
    flex-direction: column;
  }
  .column-left {
    position: relative;
    max-height: none;
  }
}

.App {
  max-width: var(--width-desktop);
  margin: auto;
  display: flex;
  min-height: 100%;
  background: linear-gradient(var(--color-input), var(--color-input))
      calc(1 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(2 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(3 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(4 * 100% / 6),
    linear-gradient(var(--color-input), var(--color-input)) calc(5 * 100% / 6);
  background-size: 1px 100%;
  background-repeat: no-repeat;
}

